<template>
  <div id="app" :class="[designerBugFix ? 'active':'']">
    <router-view name="headerBar"></router-view>
    <router-view name="mainPage" id="mainPage"></router-view>
    <myFooter v-if="showFooter"></myFooter>
  </div>
</template>

<script>
  import myFooter from './views/footer.vue'
  export default {
    name: 'app',
    data () {
      return {
        designerBugFix: false,
        showFooter: true
      }
    },
    components: {
      myFooter
    },
    methods: {
      check () {
        if (!document.getElementById('mainPage')) {
          this.showFooter = false

          setTimeout(this.check.bind(this), 100)
        } else {
          this.showFooter = true
        }
      }
    },
    watch: {
      $route () {
        // this.check()
        if (this.$route.name === 'designerHome') {
//          this.designerBugFix = true
        } else {
          this.designerBugFix = false
        }
      }
    }
  }
</script>



