// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueResource from 'vue-resource'
import App from './App'
import router from './router'
import AwesomeSwiper from 'vue-awesome-swiper'
import ElementUI from 'element-ui'
import VueSimplemde from 'vue-simplemde'
import iView from 'iview'
import $ from 'jquery'
import bowser from 'bowser'



// import '../static/css/normalize.css'
import 'normalize.css'
import '../static/css/base.css'
import 'element-ui/lib/theme-chalk/index.css'
import 'iview/dist/styles/iview.css'

// import 'jquery/dist/jquery.min'
import 'font-awesome/css/font-awesome.min.css'
import 'bootstrap/dist/css/bootstrap.min.css'

const b = bowser.getParser(window.navigator.userAgent);
const device = b.parsedResult.platform.type
if (device === 'desktop') {
  location.href = "http://regeneration.cn/";
}


Vue.config.productionTip = false

Vue.use(AwesomeSwiper)
Vue.use(VueResource)
Vue.use(ElementUI)
Vue.use(VueSimplemde)
Vue.use(iView)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})

// 微信分享

var weChatSDKInit = function () {
  var wx = window.wx
  var cUrl = window.location.href.replace(window.location.hash, '');
  $.ajax({
    url: '//wxapp.qq.com/wxjssdk-signature/sign.php',
    type: 'GET',
    data: {
      url: cUrl
    },
    dataType: 'jsonp',
    success: function (res) {
      wx.config({
        debug: false,
        appId: res.data.appId,
        timestamp: res.data.timestamp,
        nonceStr: res.data.nonceStr,
        signature: res.data.signature,
        jsApiList: [
          'onMenuShareTimeline',
          'onMenuShareAppMessage',
          'onMenuShareQQ',
          'onMenuShareWeibo',
          'onMenuShareQZone',
          'onhideOptionMenu',
          'hideAllNonBaseMenuItem'
        ]
      });
    }
  });

  wx.ready(function () {
    // 分享到朋友圈
    wx.onMenuShareTimeline({
      title: 'Regeneration', // 分享标题
      link: '', // 分享链接
      imgUrl: 'http://ovwnlb7r9.bkt.clouddn.com/1516636279000?e=1516639884&token=opKzqbb_E-y_yKjCufAu8KOiBzlhMXH2G-EXuKVj:9IdxyoW-UHHuTWcCu0uRGy6NWcY=', // 分享图标
      success: function () {
        // 用户确认分享后执行的回调函数
      },
      cancel: function () {
        // 用户取消分享后执行的回调函数
      }
    });

    // 分享给好友
    wx.onMenuShareAppMessage({
      title: 'Regeneration', // 分享标题
      desc: '展现中国时尚设计师国际水准的平台', // 分享描述
      link: '', // 分享链接
      imgUrl: 'http://ovwnlb7r9.bkt.clouddn.com/1516636279000?e=1516639884&token=opKzqbb_E-y_yKjCufAu8KOiBzlhMXH2G-EXuKVj:9IdxyoW-UHHuTWcCu0uRGy6NWcY=', // 分享图标
      type: 'link', // 分享类型,music、video或link，不填默认为link
      dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
      success: function () {
        // 用户确认分享后执行的回调函数
      },
      cancel: function () {
        // 用户取消分享后执行的回调函数
      }
    });
  });
};

weChatSDKInit();


