import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/login',
      name: 'login',
      components: {
        headerBar: null,
        mainPage: resolve => require(['@/views/Login.vue'], resolve)
      }
    },
    {
      path: '/swiper-demo',
      name: 'swiper',
      components: {
        headerBar: resolve => require(['@/views/HomeHeaderBar.vue'], resolve),
        mainPage: resolve => require(['@/views/Swiper.vue'], resolve)
      }
    },
    {
      path: '/home',
      name: 'Home',
      components: {
        headerBar: resolve => require(['@/views/HomeHeaderBar.vue'], resolve),
        mainPage: resolve => require(['@/views/Home.vue'], resolve),
      }
    },
    {
      path: '/news',
      name: 'news',
      components: {
        headerBar: resolve => require(['@/views/HomeHeaderBar.vue'], resolve),
        mainPage: resolve => require(['@/views/News.vue'], resolve)
      },
      curPathName: 'news'
    },
    {
      path: '/news/:newsId',
      name: 'newsDetail',
      components: {
        headerBar: resolve => require(['@/views/HomeHeaderBar.vue'], resolve),
        mainPage: resolve => require(['@/views/NewsDetail.vue'], resolve)
      }
    },
    {
      path: '/runway',
      name: 'runway',
      components: {
        headerBar: resolve => require(['@/views/HomeHeaderBar.vue'], resolve),
        mainPage: resolve => require(['@/views/Runway.vue'], resolve)
      },
      curPathName: 'runway'
    },
    {
      path: '/contact',
      name: 'contact',
      components: {
        headerBar: resolve => require(['@/views/HomeHeaderBar.vue'], resolve),
        mainPage: resolve => require(['@/views/Contact.vue'], resolve)
      }
    },
    {
      path: '/designer',
      name: 'designerHome',
      components: {
        headerBar: resolve => require(['@/views/HomeHeaderBar.vue'], resolve),
        mainPage: resolve => require(['@/views/Designer.vue'], resolve)
      }
    },
    {
      path: '/runway/:showId',
      name: 'show',
      components: {
        headerBar: resolve => require(['@/views/HomeHeaderBar.vue'], resolve),
        mainPage: resolve => require(['@/views/DesignerDetail.vue'], resolve)
      }
    },
    {
      path: '/designer/:designerId',
      name: 'designerPro',
      components: {
        headerBar: resolve => require(['@/views/HomeHeaderBar.vue'], resolve),
        mainPage: resolve => require(['@/views/DesignerPro.vue'], resolve)
      }
    }
  ]
})

export default router



